<template>
  <div id="app">
    <b-navbar fixed="top" toggleable="sm" type="dark" variant="dark" class="mt-1 mb-1 pt-1 pb-1">
      <b-navbar-brand href="#">Domemo</b-navbar-brand>
    </b-navbar>
    <b-container>
      <b-row>
        <b-col cols="4">
          <b-form-input v-model="player_name" placeholder="名前を入れて"></b-form-input>
        </b-col>
        <b-col cols="6">
          <b-form-input v-model="input" placeholder="送信メッセージ"></b-form-input>
        </b-col>
        <b-col cols="2">
          <b-button v-on:click="send()">送信</b-button>
        </b-col>
      </b-row>
      <b-row class="mt-2 mb-2" align-h="between">
        <b-col cols="2">
            <b-button v-on:click="start()" variant="primary">開始</b-button>
        </b-col>
      </b-row>
      <b-row>
        <div v-for="(value,index) in cards_hand" v-bind:key="index">
          <b-col cols="1">
            <b-button class="my-3"><h3>{{value}}</h3></b-button>
          </b-col>
        </div>
      </b-row>
      <b-row>
        <b-col cols="12">
          <b-card header="ステータス" class="text-left mt-3" >
            <b-card-text v-html=outMessage class="text-left">
            </b-card-text>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {
  },
  data: () => ({
    input: "",
    outMessage: "status ...",
    player_name: "",
    socket: "",
    cards_hand: [],
    cards_table: []
  }),
  created: function(){

    //  イベントリスナーからVueコンポーネントに値を渡すために一度selfで変数化しておく
    const self = this;

    self.socket = new WebSocket("wss://v4yioxwfwc.execute-api.ap-northeast-1.amazonaws.com/production");

    self.socket.onopen = function() {
        self.outMessage = "接続できました！<br>";
    };

    self.socket.onmessage = function(e) {

        var d = JSON.parse(e.data);
        //console.log("d.data:" + d.data );

        var s = self.outMessage;
        s += "Status[" + d.status +"] data[" + d.data + "]";
        if(d.status == 'waitJoin'){
            s += " room[" + d.room_name + " / id:" + d.room_id + "]";
        }
        else if( d.status == 'battle'){
          console.log('d.data:' + d.data);
          self.cards_hand = (String(d.data)).split(",");
          console.log('cards_hand:' + self.cards_hand);
        }

        self.outMessage = s + "<br>";
        //console.log("outMessage:" + self.outMessage );
    };
  },
  methods:{
    send: function() {
        this.socket.send(JSON.stringify(
            {
                "action":"sendMessage",
                "name": this.player_name,
                "data": this.input
            }
        ));
        this.input = "";
    },
    start: function() {
        this.socket.send(JSON.stringify(
            {
                "action":"gameControl",
                "name": this.player_name,
                "data": this.input,
                "command" : "start"
            }
        ));
        this.input = "";
    }
  }
}
</script>

